.sign-up {

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  position: relative;
  padding: 15px 0 0;
  margin-top: 10px;
  color: white;
  > form {
    //input:-webkit-autofill,
    //input:-webkit-autofill:hover,
    //input:-webkit-autofill:focus,
    //input:-webkit-autofill:active{
    //  -webkit-box-shadow: 0 0 0 30px #181c20 inset !important;
    //}
    .btn-auth {
      font-family: Avenir-Roman, sans-serif;
      background: #25c14a;

      color: #1d1d1d;
      cursor: pointer;
      font-size: 2em;
      padding: 5px;
      border: 0;
      transition: all 0.5s;
      border-radius: 10px;
      width: auto;
      position: relative;
      min-width: 250px;

      &::after {
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        content: "\f007";
        position: absolute;
        left: 80%;
        top: 54%;
        right: 0;
        bottom: 0;
        opacity: 0;
        transform: translate(-50%, -50%);
      }

      &:hover {
        background: #1b76ed;
        transition: all 0.5s;
        border-radius: 10px;
        box-shadow: 0px 6px 15px #0000ff61;
        color: #ffffff;
        padding: 9px  2rem 9px 1.1rem;

        &::after {
          opacity: 1;
          transition: all 0.5s;
          color: #ffffff;
        }
      }
    }
  }
}


$primary: #11998e;
$secondary: #38ef7d;
$white: #fff;
$gray: #9b9b9b;

.form__field {
  font-family: inherit;
  width: 100%;
  border: 0;
  border-bottom: 2px solid $gray;
  outline: 0;
  font-size: 1.3rem;
  color: $white;
  background: transparent;
  padding: 7px;
  font-weight: 700;
}

.form__label {
  display: block;
  font-size: 1rem;
  color: $gray;
  width: 300px;
  margin-bottom: 20px;
}

.form__field:focus {
  ~ .form__label {
    position: absolute;
    top: 0;
    display: block;
    color: $primary;
  }

  border-image: linear-gradient(to right, $primary, $secondary);
  border-image-slice: 1;
  border-width: 3px;
}

/* reset input */
.form__field {
  &:required, &:invalid {
    box-shadow: none;
  }
}